import React from 'react';
import './App.css';
import {BrowserRouter, HashRouter} from 'react-router-dom'
import { Provider } from 'react-redux';
import store from './Redux/Store';
import MyApp from './MyApp';

function App() {

 
  return (
    <HashRouter>
    <Provider store={store}>
      <MyApp/>
    </Provider>
    </HashRouter>
  );
}




export default App;